import React, { Component } from 'react'
import {
  CButton,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CRow,
} from '@coreui/react'



const REACT_APP_BASE_URL = require('../../../lib/env').default;

class WidgetList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      list: ['whatsapp', 'call-button', 'powered-by']
    }
  }
  componentDidMount = () => {

  }
  onExport() {
    window.open(`${REACT_APP_BASE_URL}/admin/page/pages-export`, '_blank');
  }
  onView(id) {
    // this.props.history.push(`/widget-setting/${id}`);
  }
  onEdit(id) {
    // this.props.history.push(`/edit/${id}`);
  }
  render() {
    var { list } = this.state;
    return (
      <>
        <CRow>
          <CCol>
            <CCard>
              <CCardHeader>Widget List</CCardHeader>
              <CCardBody>

                <table className="table table-outline mb-0 d-none d-sm-table">
                  <thead className="thead-light">
                    <tr>
                      <th>Name</th>
                      {/* <th className="text-right">Edit</th> */}
                    </tr>
                  </thead>
                  <tbody>
                    {list.map((page, i) => {

                      return <tr key={i}>

                        <td>
                          <div>{page}</div>
                        </td>
                        {/* <td className="text-right">
                          <CButton color="success" size="sm" className="ml-1" variant="outline" onClick={() => this.onView(page)}>Edit</CButton>
                        </td> */}
                      </tr>

                    })}

                  </tbody>
                </table>

              </CCardBody>
            </CCard>
          </CCol>
        </CRow>
      </>
    )
  }
}

export default WidgetList
